header {
	border: 1px solid #ffffff;
	box-sizing: border-box;
	position: fixed;
	width: 100%;
	height: 5rem;
	top: 0;
	z-index: 99;
	background-color: rgba(000, 000, 000, 0.3);
}
.contenido-header {
	display: flex;
	justify-content: space-between;
	padding: 1rem 0;
	height: 100%;

	@include desktop {
		width: 16%;
		margin-right: 1rem;
	}
	.logo {
		width: 3rem;
		position: absolute;
		left: 1rem;
		top: 1rem;

		@include desktop {
			width: 100%;
			top: 1rem;
		}
		a {
			@include desktop {
				display: flex;
				flex-direction: row;
			}
			img {
				@include desktop {
					width: 3rem;
				}
			}
			h2 {
				display: none;

				@include desktop {
					display: block;
					margin: 0;
					font-size: 1.8rem;
					color: white;
				}
			}
		}
	}

	.hamburger {
		position: absolute;
		right: 0rem;
		top: 1rem;
		z-index: 99;

		@include desktop {
			display: none;
		}
	}

	.nav-container {
		transform: translateX(110%);
		transition: all 0.5s;
		margin-top: 0;
		background-color: white;
		width: 100%;
		right: 0;
		top: 0;
		position: absolute;
		height: 100vh;

		@include desktop {
			transform: translateX(0);
			background-color: transparent;
			position: relative;
			height: auto;
			display: flex;
			align-items: center;
		}
		.nav {
			display: flex;
			flex-direction: column;
			width: 25%;
			margin: 0 auto;
			padding-top: 15rem;
			text-align: center;
			gap: 1rem;

			@include desktop {
				flex-direction: row;
				width: 100%;
				right: 0;
				padding-top: 0;
				margin: 0;
				align-items: center;
			}

			a {
				color: black;
				transition: all 0.5s;
				border-bottom: 4px solid transparent;
				transform-origin: right;

				@include desktop {
					color: white;
					font-weight: 900;
					font-size: 1rem;
				}

				&:hover {
					border-bottom: 4px solid white;
				}
			}
		}
	}

	.nav-container.open {
		transform: translateX(0);
	}
}

.hero {
	width: 100%;
	position: relative;

	img {
		width: 100%;
		object-fit: cover;
		height: 100vh;
	}

	.hero-text {
		position: absolute;
		top: 0;
		width: 100%;
		height: 100vh;
		background: rgb(0, 0, 0);
		background: -moz-linear-gradient(
			0deg,
			rgba(0, 0, 0, 0.7581232322030375) 0%,
			rgba(33, 33, 33, 0.7133053050321692) 7%,
			rgba(255, 255, 255, 0) 100%
		);
		background: -webkit-linear-gradient(
			0deg,
			rgba(0, 0, 0, 0.7581232322030375) 0%,
			rgba(33, 33, 33, 0.7133053050321692) 7%,
			rgba(255, 255, 255, 0) 100%
		);
		background: linear-gradient(
			0deg,
			rgba(0, 0, 0, 0.7581232322030375) 0%,
			rgba(33, 33, 33, 0.7133053050321692) 7%,
			rgba(255, 255, 255, 0) 100%
		);
		filter: progid:DXImageTransform.Microsoft.gradient(startColorstr="#000000",endColorstr="#ffffff",GradientType=1);

		@include desktop {
			.container {
				margin: 0;
				margin-left: 7rem;
				width: 40%;
			}
		}

		h1 {
			font-size: 1.5rem;
			padding-top: 15rem;
			color: white;
			opacity: 0.9;

			@include tablet {
				padding-top: 23rem;
			}

			@include desktop {
				padding-top: 26rem;
			}
		}

		.buttons-hero {
			display: flex;
			flex-direction: column;
			width: 95%;
			margin: 0 auto;

			@include desktop {
				flex-direction: row;
				gap: 1rem;
			}

			a {
				width: 100%;
				margin-bottom: 1rem;

				button {
					padding: 1rem 0;
					width: 100%;
					border-style: solid;
				}
			}
		}
	}
}

.btn-white {
	background: #ffffff;
}

.btn-transparent {
	background-color: transparent;
	color: white;
	border: 1px solid #fffcf8;
	transition: all 0.5s;

	&:hover {
		background-color: white;
		color: black;
	}
}
